.site-list {
  background-color: #81b3d2;
  min-width: 320px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #ffffff;
  text-transform: uppercase;
}

@media (min-width: @tablet-width) {
  .site-list {
    width: 534px;
  }
}

@media (min-width: @desktop-width) {
  .site-list {
    width: 882px;
  }
}
